<template>
    <div class="video-list">
      <div class="record__wrapper" v-if="videoList.length > 0">
        <button class="btn btn__primary" v-if="this.mediaRecorder && this.mediaRecorder.state !== 'inactive'" @click="stopRecording">녹화 중지</button>
        <button class="btn btn__primary" v-else @click="startRecording">녹화 시작</button>
        <button class="btn btn__primary" v-if="videoUrl" @click="downloadVideo">비디오 다운로드</button>
      </div>
      <div v-for="item in videoList"
           v-bind:video="item"
           v-bind:key="item.id"
           class="video-item">
        <video
          class="video"
          controls
          autoplay
          playsinline
          ref="videos"
          :muted="item.muted"
          :id="item.id" />
      </div>
    </div>
</template>

<script>
